//
// Container Mixin
//
@mixin container-fixed() {
  margin-right: auto;
  margin-left: auto;
  padding-left:  $grid-gutter-width-xs;
  padding-right: $grid-gutter-width-xs;
  @include clearfix;

  @if $grid-gutter-width-xs != $grid-gutter-width-sm {
    @media (min-width: $breakpoint-sm) {
      padding-left:  ($grid-gutter-width-sm);
      padding-right: ($grid-gutter-width-sm);
    }
  }
  @if $grid-gutter-width-sm != $grid-gutter-width-sl {
    @media (min-width: $breakpoint-sl) {
      padding-left:  ($grid-gutter-width-sl);
      padding-right: ($grid-gutter-width-sl);
    }
  }
  @if $grid-gutter-width-sl != $grid-gutter-width-md {
    @media (min-width: $breakpoint-md) {
      padding-left:  ($grid-gutter-width-md);
      padding-right: ($grid-gutter-width-md);
    }
  }
  @if $grid-gutter-width-md != $grid-gutter-width-lg {
    @media (min-width: $breakpoint-lg) {
      padding-left:  ($grid-gutter-width-lg);
      padding-right: ($grid-gutter-width-lg);
    }
  }
  @if $grid-gutter-width-lg != $grid-gutter-width-xl {
    @media (min-width: $breakpoint-xl) {
      padding-left:  ($grid-gutter-width-xl);
      padding-right: ($grid-gutter-width-xl);
    }
  }
}



//
// Container Fixed
//
.container {
  @include container-fixed;

  @media (max-width: $breakpoint-sm - 1px) {
    max-width: $container-xs-max-width;
  }
  @media (min-width: $breakpoint-sm) {
    width: $container-sm;
  }
  @media (min-width: $breakpoint-sl) {
    width: $container-sl;
  }
  @media (min-width: $breakpoint-md) {
    width: $container-md;
  }
  @media (min-width: $breakpoint-lg) {
    width: $container-lg;
  }
  @media (min-width: $breakpoint-xl) {
    width: $container-xl;
  }
}



//
// Container Fluid
//
.container-fluid {
  @include container-fixed;
}



//
// Row
//
.row {
  margin-left: -10px;
  margin-right: -10px;
  @include clearfix;
}

  @media (min-width: 640px) {
    .row {
      margin-left: -15px;
      margin-right: -15px;
    }
  }

  @media (min-width: 1520px) {
    .row {
      margin-left: -20px;
      margin-right: -20px;
    }
  }



